body {
  background-color: var(--color-slate-950);
  color: var(--color-slate-100);
}
.neural-node {
  position: absolute;
  width: 8px;
  height: 8px;
  background: radial-gradient(circle, #7c3aed, #4f46e5);
  border-radius: 50%;
  animation: fade-and-move 15s ease-in-out forwards;
}
.circuit-line {
  position: absolute;
  height: 2px;
  background: linear-gradient(to right, #a78bfa, #c084fc);
  opacity: 0.7;
  animation: flicker-line 12s ease-in-out forwards;
}
.data-stream {
  position: absolute;
  width: 2px;
  height: 100vh;
  background: linear-gradient(to bottom, #67e8f9, #7c3aed);
  animation: stream-flow 10s linear forwards;
}
@keyframes fade-and-move {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.5);
  }
}
@keyframes flicker-line {
  0% {
    opacity: 0;
    transform: scaleX(0.8);
  }
  50% {
    opacity: 0.7;
    transform: scaleX(1);
  }
  100% {
    opacity: 0;
    transform: scaleX(0.8);
  }
}
@keyframes stream-flow {
  0% {
    transform: translateY(-100%);
    opacity: 0.3;
  }
  100% {
    transform: translateY(100%);
    opacity: 0;
  }
}
.particle {
  position: absolute;
  top: 0;
  background: radial-gradient(circle, #ffffff88, #ffffff22);
  border-radius: 9999px;
  animation: float-down linear forwards;
  opacity: 0.8;
}
@keyframes float-down {
  0% {
    transform: translateY(0);
    opacity: 0.8;
  }
  100% {
    transform: translateY(100vh);
    opacity: 0;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(2deg);
  }
}
@keyframes rotate-slow {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.animate-float {
  animation: float 6s ease-in-out infinite;
}
.animate-float-delayed {
  animation: float 8s ease-in-out infinite;
  animation-delay: 1.5s;
}
.animate-rotate-slow {
  animation: rotate-slow 20s linear infinite;
}
.tech-grid {
  background-image: linear-gradient(to right, rgba(255,255,255,0.05) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(255,255,255,0.05) 1px, transparent 1px);
  background-size: 40px 40px;
}
.glow-box {
  box-shadow: 0 0 12px rgba(165, 105, 255, 0.4), 0 0 32px rgba(165, 105, 255, 0.2);
}
.cyber-border {
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(8px);
}
.gradient-text {
  background: linear-gradient(to right, #a78bfa, #8b5cf6, #7c3aed);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.glow-text {
  text-shadow: 0 0 10px #c084fc, 0 0 20px #8b5cf6;
}
